.topfront-background {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.5);
}

.contactImage.contactImage {
    width: 100%;
    height: 500px;
    object-fit: cover;
    object-position: 50% 40%;
    z-index: -1;
}

.contactIntro.contactIntro {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background-color: rgba(255, 255, 255, 0.0);
    padding: 30px;
    border-color: #2ab400;
}